<template>
  <div id="app">
    <router-view v-wechat-title='$route.meta.title' v-cloak />

    <!-- x -->
    <bottom-nav></bottom-nav>

    <!-- loading -->
    <div class="spinner-wrap" v-if="loadingPage">
        <mt-spinner type="triple-bounce"></mt-spinner>        
    </div>
  </div>
</template>

<script>
require('assets/lib/flexible.js')
require('assets/public/reset.css')
require('assets/public/common.css')
import bottomNav from 'components/bottom-nav'
import Http from 'assets/lib/http.js'
import wx from 'weixin-js-sdk'
import {getCookie, deleteCookie} from 'assets/lib/publicMethod'

export default {
    name: 'app',
    data () {
        return {
            orgId      : '',
            loadingPage: true,
            appid      : '',
            signature  : '',
            noncestr   : '',
            timestamp  : ''
        }
    },
    beforeCreate() {
        
    },
    created() {
        this.getCommonConfig();
        this.getweixintoken();
    },
    mounted() {
        this.delCookie()
    },
    methods: {
        delCookie() {
            var REDIRECT = getCookie('REDIRECT');
            if (REDIRECT) {
                if (sessionStorage.orgId === 'shankunzhongyan') {
                    deleteCookie('REDIRECT', '.sk.saotx.cn');
                } else if (sessionStorage.orgId === 'henanzhongyan') {
                    deleteCookie('REDIRECT', '.weiop.taozuike.com');
                }
            }
        },
        getCommonConfig() {
            var me = this;
            Http.get('/seller-web/seller/queryBaseConfig')
                .then(res => {
                    var Data = res.data;
                    if (Data.ok) {
                        var data = Data.data;
                        me.orgId = data.orgId;
                        sessionStorage.setItem('orgId', me.orgId);
                        if (me.orgId === 'shankunzhongyan') {
                            require('assets/theme/shankun.css');
                            this.companyIdClass.classList.add('shankun')
                        } else {
                            require('assets/theme/henan.css');
                            this.companyIdClass.classList.add('henan')
                        }
                    } else {
                        alert(Data.msg);
                        return 
                    }
                })
        },
        getweixintoken () {
            let me = this;
            Http.get('/seller-web/wechat/open/jstoken')
              .then(res => {
                const Data = res.data;
                if (Data.ok) {
                    let data = Data.data;
                    me.appid = data.appid;
                    me.noncestr = data.noncestr;
                    me.signature = data.signature
                    me.timestamp = data.timestamp;
                    sessionStorage.setItem('appid', data.appid);
                    sessionStorage.setItem('noncestr', data.noncestr);
                    sessionStorage.setItem('signature', data.signature);
                    sessionStorage.setItem('timestamp', data.timestamp);

                    wx.config({
                        debug: false, // ,apialert pclogpc
                        appId: me.appid, // corpid
                        timestamp: me.timestamp, // 
                        nonceStr: me.noncestr, // 
                        signature: me.signature,// 1
                        jsApiList: [
                            'checkJsApi',
                            'onMenuShareTimeline',
                            'onMenuShareAppMessage',
                            'onMenuShareQQ',
                            'onMenuShareWeibo',
                            'onMenuShareQZone',
                            'scanQRCode',
                            'chooseWXPay'
                        ] // JSJS2
                    });

                    // configwx.ready
                    me.$bus.emit('configready', true);

                    wx.ready(function(){
                        me.share()
                        // configreadyconfigconfigreadyready
                    });
                    wx.error(function(res){
                        // alert('app' + JSON.stringify(res));
                        // configerrorconfigdebugresSPA
                    });
                }
            })
        },
        share() {
            var shareTitle = 'title',
                shareLink = '',
                shareImg = '',
                shareDesc = '';

            wx.onMenuShareTimeline({
                title: shareTitle, // 
                link: shareLink, // 
                imgUrl: shareImg,
                desc: shareDesc,
                success: function () {

                }
            });
            wx.onMenuShareAppMessage({
                title: shareTitle, // 
                link: shareLink, // 
                imgUrl:shareImg,
                desc: shareDesc, // 
                trigger: function (res) {

                },
                success: function (res) {

                },
                cancel: function (res) {

                },
                fail: function (res) {

                }
            });
            wx.onMenuShareQQ({
                title: shareTitle, // 
                link: shareLink, // 
                imgUrl:shareImg,
                desc: shareDesc, // 
                trigger: function (res) {

                },
                success: function (res) {

                },
                cancel: function (res) {

                },
                fail: function (res) {

                }
            });
            wx.onMenuShareWeibo({
                title: shareTitle, // 
                link: shareLink, // 
                imgUrl:shareImg,
                desc: shareDesc, // 
                trigger: function (res) {

                },
                success: function (res) {

                },
                cancel: function (res) {

                },
                fail: function (res) {

                }
            });
            wx.onMenuShareQZone({
                title: shareTitle, // 
                desc: shareDesc, // 
                link: shareLink, // 
                imgUrl: shareImg, // 
                success: function () {

                },
                cancel: function () {

                }
            });
        }
    },
    computed: {
        companyIdClass () {
            return document.getElementById('app')
        }
    },
    components: {
      bottomNav
    }
}
</script>
<style lang="scss">
[v-cloak] {
    display: none;
}
</style>



