<template>
	<div class="myqresult border-box">
		<div class="info border-box">
			<div class="left">
				<div class="avatar certified"><a href="javascript:;"><img :src="user.headImg" alt=""></a></div>
				<div class="certificate"><img :src="user.licenceImg" alt=""></div>
			</div>
			<div class="right">
				<div class="item">
					<span class="name">:</span>
					<div class="content">{{user.ownerName}}<span></span></div>
				</div>
				<div class="item">
					<span class="name">:</span>
					<div class="content">{{user.phoneNo}}<span></span></div>
				</div>
				<div class="item">
					<span class="name">:</span>
					<div class="content">{{user.shopName}}<span></span></div>
				</div>
				<div class="item">
					<span class="name">:</span>
					<div class="content">{{user.addrDetail}}<span></span></div>
				</div>
				<div class="certificate-number">
					<span class="name"></span>:
					<div class="content">{{user.licenceNo}}</div>
				</div>
			</div>
		</div>
		<button class="scan theme-bg-color" @click="scan"><img src="" alt=""></button>
		<p class="tip font-color-lighter">10%</p>
	</div>
</template>
<script>
import Http from 'assets/lib/http.js'
import wx from 'weixin-js-sdk'

export default {
	name: 'Myresult',
	data() {
		return {
			sellerId: sessionStorage.getItem('sellerId') || this.$route.query.sellerId || '',
			user: {}
		}
	},
	created() {
		this.showUserInfo()
	},
	methods: {
		showUserInfo() {
			Http.get('/seller-web/seller/main/' + this.sellerId)
			.then(res => {
				const Data = res.data;
				if (Data.ok) {
					this.$parent.loadingPage = false; //loading
					this.user = Data.data.sellerInfo;
				}
			})
		},
		scan() {
			// 
			wx.config({
                debug: false,
                appId: sessionStorage.getItem('appid'),
                timestamp: sessionStorage.getItem('timestamp'),
                nonceStr: sessionStorage.getItem('noncestr'),
                signature: sessionStorage.getItem('signature'),
                jsApiList: ['scanQRCode']
            });
            wx.ready(function () {
                wx.scanQRCode({
                    needResult: 0, // 01
                    scanType: ["qrCode", "barCode"], 
                    // 
                    success: function(res) {
                         // needResult  1 
                    }
                })
            })
		}
	}
}
</script>
<style lang="scss">
.myqresult {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	.info {
		display: -webkit-box;
		-webkit-box-orient: horizontal;
		display: flex;
		margin: 6rem auto 0;
		padding: 0 .3rem;
		width: 7.8667rem;
		height: 5.33rem;
		.left {
			margin-right: .3rem;
			width: 2rem;
			.avatar {
				position: relative;
				width: 100%;
				a {
					overflow: hidden;
					display: block;
					margin: 0 auto;
					width: 1.44rem;
					height: 1.44rem;
					line-height: 1.44rem;
					-webkit-border-radius: 50%;
					-moz-border-radius: 50%;
					-ms-border-radius: 50%;
					border-radius: 50%;
					border: 1px solid #ccc;
					img {
						width: 100%;
						vertical-align: middle;
					}
				}
			}
			.certificate {
				margin: .4rem auto;
				width: 1.76rem;
				height: 1.2rem;
				border: 1px solid #ccc;
				text-align: center;
				img {
					max-width: 100%;
					max-height: 100%;
				}
			}
		}
		.right {
			flex: 1;
			color: #E4341D;
			.name {
				font-weight: 500;
			}
			.item {
				overflow: hidden;
				margin-bottom: .18rem;
				font-size: .293rem;
				.name {
					float: left;
					width: 1.65rem;
					/*text-align-last: justify;*/
					text-align: justify;
				}
				.content {
					margin-top: .05rem;
					float: right;
					width: calc(100% - 1.7rem);
					text-align: left;
					line-height: 1.2;
					span {
						display: inline-block;
					}
				}
			}
			.certificate-number {
				.content {
					line-height: 1.8;
				}
			}
		}
	}
	.scan {
		display: block;
		margin: 1rem auto .4rem;
		width: 4.54667rem;
		height: 1.1733rem;
		font-size: .613rem;
		color: #fff;
		-webkit-border-radius: 4px;
		-moz-border-radius: 4px;
		-ms-border-radius: 4px;
		border-radius: 4px;
	}
	.tip {
		text-align: center;
		font-size: .32rem;
	}
}
</style>


