<template>
	<div class="activation">
    <!-- kk -->
		<div v-if="!isYanTai">
            <div class="item-wrap">
                <div class="item">
                    <span class="name"></span>
                    <div class="content">{{user.ownerName}}</div>
                </div>
                <div class="item">
                    <span class="name"></span>
                    <div class="content">{{user.phoneNo}}</div>
                </div>
                <div class="item">
                    <span class="name"></span>
                    <div class="content">{{user.shopName}}</div>
                </div>
                <div class="item">
                    <span class="name"></span>
                    <div class="content">{{user.addrDetail}}</div>
                </div>
                <div class="item certificate">
                    <span class="name"></span>
                    <div class="content">{{user.licenceNo}}</div>
                </div>
                <div class="item">
                    <span class="name"></span>
                    <div class="content">{{user.districtName}}</div>
                </div>
                <div class="item">
                    <span class="name"></span>
                    <div class="content">{{user.commercialName}}</div>
                </div>
            </div>

            <button class="confirm theme-bg-color_lighter" :disabled="confirmDisable" :class="{'disabled-btn': confirmDisable}" @click="confirm"></button>

            <!--  -->
            <mt-popup
            class="uni-pop pop border-box"
            v-if="codePop"
            v-model="codePop"
            :closeOnClickModal="false"
            position="center">
            <pop-modal>
              <div class="verify">
                <label for="">
                  <input class="vcode border-box" type="text" v-model="vcode" :disabled="vcodeInputDisable">
                  <input class="theme-bg-color_lighter vcode-btn border-box" type="button" v-model="vcodeBtnMsg" readonly="true" @click="send" :disabled="vcodeBtnDisable" :class="{'disabled-btn': vcodeBtnDisable}">
                </label>
              </div>
              <p class="font-color tip1"></p>
              <p class="tip3"><span class="font-color">{{user.phoneNo}}</span></p>
              <button slot="button" class="theme-bg-color_lighter" :disabled="vcodeVerifyDisable" :class="{'disabled-btn': vcodeVerifyDisable}" @click="verify"></button>
            </pop-modal>
            </mt-popup>
        </div>

        <!-- / -->
        <mt-popup
          class="uni-pop pop border-box"
          v-if="confirmPop"
          v-model="confirmPop"
          :class="{success: successActivate, fail: !successActivate}"
          :closeOnClickModal="false"
          position="center">
          <pop-modal>
            <p class="font-color tip1">{{activateState.text}}</p>
            <p class="tip3">{{activateState.tips}}</p>
            <button slot="button" class="theme-bg-color_lighter" @click="iget"></button>
          </pop-modal>
        </mt-popup>

        <!--  -->
        <div v-if="isYanTai" class="zhazhazha">
          <!--  -->
          <mt-popup
            class="uni-pop pop"
            v-model="ytCodePop"
            :closeOnClickModal="false"
            position="center"
            >
            <pop-modal>
              <!--  -->
              <input class="yt-licenceNo" type="text" v-model="user.licenceNo" disabled="true" placeholder=""/>

              <!--  -->
              <label for="">
                <input class="yt-phone vcode" type="text" v-model="user.phoneNo" disabled="true" placeholder=""/>
                <input class="theme-bg-color_lighter vcode-btn" type="button" v-model="vcodeBtnMsg" @click="send" :disabled="ytVcodeBtnDisable" :class="{'disabled-btn': ytVcodeBtnDisable}">
              </label>

              <!--  -->
              <input class="yt-vcode" @input="ytVcodeChange" type="text" v-model="ytVcode" placeholder=""/>

              <button slot="button" class="theme-bg-color_lighter" :disabled="ytVcodeVerifyDisable" :class="{'disabled-btn': ytVcodeVerifyDisable}" @click="verify"></button>
            </pop-modal>
          </mt-popup>
        </div>
	</div>
</template>

<script>
import Http from 'assets/lib/http.js'
import popModal from 'components/pop-modal'
import {getQueryString} from 'assets/lib/publicMethod'

export default {
	name: 'Activation',
	data () {
		return {
      // 
			vcode: '',
			codePop: true, // 
			confirmPop: false, // or
			successActivate: false, // 
			confirmDisable: true, // 
			vcodeInputDisable: true, // 
			vcodeBtnDisable: false, // 
			vcodeVerifyDisable: true, // 
			vcodeBtnMsg: '',
			count: 60,
			timer: null,
			sellerId: sessionStorage.getItem('sellerId') || this.$route.query.sellerId || '',
			user: {},
			activateState: {
				text: ''
			},

            // 
            isYanTai: this.$route.query.actFlag == 't1', //  t1
            ytCodePop: true, // 
            ytVcode: '', // 
            ytVcodeBtnDisable: false, // 
            ytVcodeVerifyDisable: true, // 
            sellerInfo: {}
		}
	},
	created () {
        this.getRetailerInfo();
        if(this.isYanTai) {
            this.$parent.loadingPage = false; //loading
        }
	},
	mounted () {

	},
	beforeUpdate () {

	},
	updated () {

	},
	methods: {
		getRetailerInfo() {
			Http.get('/seller-web/consumer/seller/detail?sellerId=' + this.sellerId)
            .then(res => {
                var Data = res.data;
                if (Data.ok) {
                    this.$parent.loadingPage = false; //loading
                    this.user = Data.data;
                    if (this.isYanTai) {
                        this.sellerInfo = Data.data;
                    }
                }
            })
		},

		// 
		send() {
        var me = this;
        function getVerify (phone) {
            var interval = window.setInterval(function() {
                if ((me.count--) == 0) {
                    me.count = 60;
                    me.vcodeBtnDisable = false;
                    me.ytVcodeBtnDisable = false;
                    me.vcodeBtnMsg = '';
                    window.clearInterval(interval);
                } else {
                    me.vcodeBtnMsg = me.count + '';
                }
            }, 1000);
            Http.get('/admin/login/getDynamicCode?oc=1&mobile=' + phone)
                .then(res => {
                    var Data = res.data
                    if (Data.ret != 200000) {
                        alert(Data.message)
                    } else {
                        me.user.vcode = Data.data
                    }
                })
        }

        //!me.vcodeBtnDisable
        if (!this.isYanTai) {
            me.vcodeBtnDisable = true;
            me.vcodeInputDisable = false;
            me.vcodeVerifyDisable = false;

            getVerify(me.user.phoneNo);
        } else { // 
            me.ytVcodeBtnDisable = true;
            getVerify(me.user.phoneNo);
        }
    },

    // input
    ytVcodeChange () {
        if (this.ytVcode != '') {
            this.ytVcodeVerifyDisable = false;
        } else {
            this.ytVcodeVerifyDisable = true;
        }
    },

    // 
    verify () {
        var me = this;
        function confirmSub (phone, code) {
            Http.get('/admin/login/checkDynamicCode?mobile=' + phone + '&code=' + code)
                .then(res => {
                    var Data = res.data
                    // 
                    if (!me.isYanTai) {
                        if (Data.data) {
                            me.codePop = false;
                            me.confirmDisable = false;
                        } else {
                            alert(Data.message);
                            this.vcodeVerifyDisable = false;
                            return
                        }
                    } else { // 
                        if (Data.data) { // true
                        // 
                            me.confirm();
                        } else {
                            alert(Data.message);
                            return
                        }
                    }
                })
            }

        // !this.vcodeVerifyDisable
        if (!this.isYanTai) {
            this.vcodeVerifyDisable = true;
            confirmSub(me.user.phoneNo, me.vcode);
        }

        // 
        if (this.isYanTai) {
            this.ytVcodeVerifyDisable = true;
            confirmSub(me.user.phoneNo, me.ytVcode);
        }
    },

    // 
    confirm () {
        this.confirmDisable = true;
        var me = this;
        // 
        if (!this.isYanTai) {
            var phone = me.user.phoneNo,
            code = me.vcode;
        } else { // 
            var phone = me.user.phoneNo,
            code = me.ytVcode;
        }
        Http.get('/seller-web/consumer/active?sellerId=' + me.sellerId + '&valid=' + code + phone + '1' + code.length)
            .then(res => {
                var Data = res.data;
                me.confirmPop = true;
                if (Data.ok) {
                    // 
                    me.successActivate = true;
                    me.activateState.text = '';
                    if (!me.isYanTai) {
                        me.activateState.tips = ''
                    } else {
                        me.activateState.tips = ''
                        this.sellerInfo.authStatus = 2;
                        sessionStorage.setItem('user', JSON.stringify(me.sellerInfo))
                    }
                } else {
                    // 
                    me.activateState.text = '';
                    me.activateState.tips = ''
                }
            })
    },

    // 
    iget () {
        if (this.successActivate) {
            // 
            if (!this.isYanTai) {
                this.$router.push({path:'/retailer/index'})
            } else {
                // 
                // 
                this.$router.push({path:'/retailer/info'})
            }
        } else {
            this.confirmPop = false;
            this.confirmDisable = false
        }
    }
	},
	computed: {

	},
	components: {
		popModal
	}
}
</script>

<style lang="scss" scoped>
  * {
    box-sizing: border-box;
  }
  .theme-bg-color_lighter {
    transition: all 0.3s ease-in-out;
    &:active {
      transform: scale(0.98);
    }
  }

.activation {
	.item-wrap {
		background: #fff;
	}
	.item {
		overflow: hidden;
		padding-left: .3rem;
		padding-right: .3rem;
		line-height: 1.3rem;
		font-size: .42667rem;
		span {
			float: left;
			width: 3rem;
			color: #666;
			text-align-last: justify;
		}
		.content {
			float: right;
			width: 5.8rem;
		}
		&:not(:last-child) {
			border-bottom: 1px solid rgba(204,204,204,.4);
		}
	}
	.certificate {
		line-height: 2;
	}
	.confirm {
		position: absolute;
		left: 50%;
		bottom: 1rem;
		-webkit-transform: translateX(-50%);
		-moz-transform: translateX(-50%);
		-ms-transform: translateX(-50%);
		transform: translateX(-50%);
		width: 9.33rem;
		height: 1.1733rem;
		line-height: 1.174rem;
		color: #fff;
		font-size: .48rem;
		text-align: center;
		-webkit-border-radius: 4px;
		-moz-border-radius: 4px;
		-ms-border-radius: 4px;
		border-radius: 4px;
	}
	.mint-popup {
    /*  S*/
    .yt-licenceNo, .yt-vcode {
      width: 7.24rem;
      height: 1.04rem;
      font-size: .48rem;
      background: #CACACA;
      padding: 0 0.2rem;
    }
    .yt-vcode {
      background: #fff;
      border: 1px solid #CACACA;
      margin: 0.7rem 0 0.3rem;
    }
    .yt-phone {
      padding: 0 0.2rem;
      text-align: left !important;
    }
    /*  E*/

		label {
			overflow: hidden;
			display: block;
			margin: .8rem auto 0;
			width: 7.24rem;
			height: 1.04rem;
			font-size: 0;
			.vcode {
				float: left;
				margin: 0;
				text-align: center;
				width: 4.6rem;
				height: 100%;
				font-size: .48rem;
				background: #CACACA;
				-webkit-border-radius: 0;
				-moz-border-radius: 0;
				-ms-border-radius: 0;
				border-radius: 0;
			}
			.vcode-btn {
				float: left;
				margin: 0;
				width: calc(100% - 4.7rem);
				height: 1.04rem;
				padding: 0.03rem .05rem;
				font-size: .42rem;
				text-align: center;
				color: #fff;
				-webkit-border-radius: 0;
				-moz-border-radius: 0;
				-ms-border-radius: 0;
				border-radius: 0;
				-webkit-user-select: none;
				user-select: none;
			}
		}
		img {
			width: 2.667rem;
			height: 1.92rem;
		}
		.tip1 {
			margin-top: .6rem;
			margin-bottom: .3rem;
		}
		.tip3 {
			margin-bottom: 1.2rem;
		}
	}
}
</style>



