<template>
  <!---->
	<div class="ytscancode">
    <button class="theme-bg-color again-btn" @click="scanAgain"></button>
	</div>
</template>

<script>
  import wx from 'weixin-js-sdk'

  export default {
    name: 'YantaiScanCode',
    data () {
      return {

      }
    },
    created() {
      this.$parent.loadingPage = false; //loading
    },
    mounted() {
      let me = this;
      this.$bus.on('configready', function (val) {
        if (val) {
          me.scan()
        } else {
          console.log('valtrue')
        }
      })
    },
    methods: {
      scan() {
        wx.ready(function () {
          console.log('yantai.vuescan')
          wx.scanQRCode({
            needResult: 0, // 01
            desc: 'scanQRCode desc',
            scanType: ["qrCode", "barCode"], // 
            success: function(res) {
            },
            fail: function (res) {
              if (res.errMsg.indexOf('function_not_exist') > 0) {
                alert('')
                return
              }
            }
          })
        })

        wx.error(function(res){
          console.log('', JSON.stringify(res))
        })
    },
    scanAgain() {
      this.scan()
    }
	},
}
</script>

<style lang="scss" scoped>
  .again-btn {
    width: 200px;
    height: 50px;
    display: block;
    font-size: 20px;
    line-height: 50px;
    margin: 200px auto;
  }

</style>



