<template>
	<div class="warehouse-wrap">
		<div class="warehouse">
			<p class="name">{{text}}</p>
			<p class="tip">{{tip}}</p>
			<div class="btns">
				<button class="theme-bg-color" @click="gotocredit"></button>
				<button class="theme-bg-color" @click="scanputinStorage"></button>
			</div>
		</div>
	</div>
</template>

<script>
import Http from 'assets/lib/http.js'
import wx from 'weixin-js-sdk'

export default {
	data () {
		return {
			text: '',
			tip: '',
            appid: '',
            noncestr: '',
            signature: '',
            timestamp: ''
		}
	},
	created() {
        this.$parent.loadingPage = false; //loading
        // this.scan()
	},
    mounted() {
        let me = this;
        this.$bus.on('configready', function (val) {
            if (val) {
                me.scan()
            } else {
                console.log('warehousingvaltrue')
            }
        })
    },
    updated() {

    },
	//
	methods: {
		scanputinStorage() {
            this.scan()
		},
        scan() {
        	var me = this;
            wx.ready(function () {
                console.log('warehousing.vuewx.readyscan');
                wx.scanQRCode({
                    needResult: 1, // 01
                    desc: 'scanQRCode desc',
                    scanType: ["qrCode"], // 
                    success: function(res) {
                        me.successScan(res)
                    },
                    fail: function (res) {
                        me.failScan();
                        if (res.errMsg.indexOf('function_not_exist') > 0) {
                           alert('')
                           return
                        }
                    }
                })
            })
        },
        successScan(res) {
        	var me = this,
        		resultStr = res.resultStr,
                sellerId = sessionStorage.getItem('sellerId') || this.$route.query.sellerId || '';
        	Http.get('/seller-web/consumer/canInstoreQr', {
                params: {
                    content: resultStr,
                    sellerId: sellerId
                }
        	}).then(res => {
        		var Data = res.data;
                if (Data.ok) {
                    me.text = '1';
                    me.tip = '';
                } else {
                    me.text = Data.msg;
                    me.tip = '';
                    // 
                    // 
                }
        	})
        },
        failScan() {
			this.text = ''
        },
        gotocredit() {
            var hostname = location.hostname;
            if (hostname == 'sk.saotx.cn') {
                // 
                location.href = '/a/p/scoremall002-score-integral.html?t=' + (+new Date)
            } else if (hostname == 'weiop.taozuike.com') {
                // 
                location.href = '/a/p/score-integral.html?sourceId=3001?t=' + (+new Date);
            } else if (hostname === 'hbz.saotx.cn') {
                // 
                location.href = '/app-hebei/views/menus/shop-integral.html?t=' + (+new Date)
            }
        }
	}
}
</script>

<style lang="scss" scoped>
@mixin border-radius($num) {
	-webkit-border-radius: $num;
	-moz-border-radius: $num;
	-ms-border-radius: $num;
	border-radius: $num;
}
.name {
	padding-top: 7rem;
	margin: 0 auto .5rem;
	width: 4.8rem;
	line-height: 1.3;
	text-align: center;
	color: #1F1E23;
	font-size: .48rem;
}
.tip {
	text-align: center;
	color: #666;
	font-size: .37rem;
}
.btns {
	margin: 1.2667rem auto 0;
	width: 8.4rem;
}
button {
	width: 3.5rem;
	height: 1.02rem;
	@include border-radius(5px);
	font-size: .48rem;
	color: #fff;
	&:last-child {
		margin-left: 1.3rem;
	}
}
</style>



